import cx from 'classnames';
import styles from './index.module.scss';
import { IconArrowRight } from '@douyinfe/semi-icons';
import { useNavigate } from '@edenx/runtime/router';

interface Props {
  title: string;
  data: {
    name?: string;
    score?: string | undefined;
  }[];
  scoreType: string;
  rankType: string;
}

const beforeBrandIndex = 2;

const RankList = (props: Props) => {
  const { title, data, scoreType, rankType } = props;
  const navigate = useNavigate();

  return (
    <div>
      <header className={styles.title}>{title}</header>
      <section className={styles.content}>
        <div className={styles['rank-list-header']}>
          <div>排名</div>
          <div className={styles['score-container']}>{scoreType}</div>
        </div>
        <ul className={styles.list}>
          {data
            ?.sort((aItem, bItem) => {
              const aScore = Number(aItem.score);
              const bScore = Number(bItem.score);
              if (aScore > bScore) {
                return -1;
              }
              if (aScore < bScore) {
                return 1;
              }
              return 0;
            })
            .map((item, idx) => (
              <li
                className={styles['rank-item']}
                key={`${item.name}__${item.score}`}
                onClick={() => {
                  navigate(`/rank/detail?rank=${rankType}&models=${item.name}`);
                }}
              >
                <div
                  className={cx(
                    styles.brand,
                    styles[
                      `brand-${idx <= beforeBrandIndex ? idx + 1 : 'other'}`
                    ],
                  )}
                >
                  <div className={styles['brand-num']}>{idx + 1}</div>
                </div>
                <div className={styles.info}>
                  <div className={styles.name}>{item.name}</div>
                </div>
                <div className={styles.score}>{item.score}</div>
              </li>
            ))
            .slice(0, 10)}
        </ul>
        <div
          className={styles['rank-list-footer']}
          onClick={() => {
            navigate(`/rank/detail?rank=${rankType}`);
          }}
        >
          <span className={styles.text}>
            View All
            <IconArrowRight color="white" size="small" />
          </span>
        </div>
      </section>
    </div>
  );
};

export default RankList;
