import { useMemo } from 'react';
import RankList from './components/RankList';
import styles from './index.module.scss';
import rankBg from '@/assets/images/rank-bg.png';
import { rankData } from '@/assets/json/data';

const Rank = () => {
  const formattedRankData = useMemo(() => {
    const matchRankData = rankData.match.tables.overall.data
    //   .slice(0, 10)
      .map(item => ({
        name: item.Method,
        score: (item.Scores.overall?.SRCC || 0).toFixed(3),
      }));

    const structureRankData = rankData.structure.tables.default.data
    //   .slice(0, 10)
      .map(item => ({
        name: item.Method,
        score: (item.Scores.overall.F1_Score || 0).toFixed(3),
      }));

    const overallRankData = rankData.overall.tables.default.data
    //   .slice(0, 10)
      .map(item => ({
        name: item.Model,
        score: (item.Scores['overall alignment'].score || 0).toFixed(3),
      }));

    return { matchRankData, structureRankData, overallRankData };
  }, [rankData]);

  return (
    <div>
      <header className={styles.header}>
        <div className={styles.content}>
          <div className={styles.info}>
            <div className={styles.title}>EvalMuse-40K Leaderboard</div>
            <div className={styles.intro}>
              We show the evaluation ability of different models on image-text
              alignment, structural problems. And we evaluated the image-text
              alignment ability of multiple T2I models using our proposed
              image-text alignment evaluation method FGA-BLIP2.
              {/* 我们评估了不同模型在图文匹配度，结构性评估上的能力。并利用我们提出的图文匹配度评估方法对多种T2I模型进行了图文匹配度能力的评估 */}
            </div>
          </div>
          <div className={styles.img}>
            <img width={489} height={313} src={rankBg} />
          </div>
        </div>
      </header>
      <section className={styles.main}>
        <RankList
          title="Image-Text Alignment Evaluation"
          data={formattedRankData.matchRankData}
          scoreType="SRCC"
          rankType="match.overall"
        />
        <RankList
          title="Structure Evaluation"
          data={formattedRankData.structureRankData}
          scoreType="F1_Score"
          rankType="structure"
        />
        <RankList
          title="T2I Model Evaluation on Iamge-Text Alignment"
          data={formattedRankData.overallRankData}
          scoreType="Alignment"
          rankType="overall"
        />
      </section>
    </div>
  );
};

export default Rank;
